.background-item  *{
    color: #fff ;
    -webkit-transition: all 400ms ease-in-out;
	-moz-transition: all 400ms ease-in-out;
	-o-transition: all 400ms ease-in-out;
	-ms-transition: all 400ms ease-in-out;
	transition: all 400ms ease-in-out;  
}
.background-item{
    position: relative;
    overflow: hidden;
    margin: auto;
}
.background-item, .img-scroll, .item-scroll, .button-background{
    width: 250px;
    height: 300px;
    
   
}

.item-scroll > *{
    margin-bottom: 10px;
    -webkit-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
    display: block ;
    color: rgb(255, 255, 255);
    -webkit-transform: translate3d(-100%, 0, 0) translate3d(-2em, 0, 0);
	transform: translate3d(-100%, 0, 0) translate3d(-2em, 0, 0);
}

.item-scroll{  
    position: absolute;
    top: 0;
    right: 0;
    color: #1a1a1a;
    background-color: rgba(174, 218, 103, 0.0);
    z-index: 9999999;
}

.button-background{
    position: absolute;
    background-color: rgba(255, 244, 144, 0.007);
    top: 0;
    right: 0;
    z-index: 999999;
    border: solid 10px rgba(255, 255, 255, 0.8);
}

.img-scroll{  
    position: absolute;
    overflow: hidden;
    z-index: 9999;
}
.img-b{
    
    width: 100%;
    -webkit-transform: scale(1);
	-moz-transform: scale(1);
	-o-transform: scale(1);
	-ms-transform: scale(1);
    transform: scale(1);
    opacity: 0.8;
}
.background-item:hover .button-background {
    -webkit-animation-name: zoomOut;
    animation-name: zoomOut;
}
.background-item:hover .item-scroll {
    background-color: rgba(80, 112, 34, 0.95);
    -webkit-animation-name: flipInX;
    animation-name: flipInX;
}
.background-item:hover .img-b{
    -webkit-transform: scale(1.5);
	-moz-transform: scale(1.5);
	-o-transform: scale(1.5);
	-ms-transform: scale(1.5);
	transform: scale(1.5);
}
.item-scroll h3{  
    -webkit-transition-delay: 0.02s;
	transition-delay: 0.02s;  
}
.item-scroll strong{  
    -webkit-transition-delay: 0.08s;
	transition-delay: 0.08s; 
}
.item-scroll div{ 
    -webkit-transition-delay: 0.20s;
	transition-delay: 0.20s;  
}

.background-item:hover .item-scroll h3, 
.background-item:hover .item-scroll strong, 
.background-item:hover .item-scroll div{
    opacity: 1;
	transform: translate3d(0, 0, 0);
	-webkit-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);  
}
.btn-op-5{
    
    display: inline-block;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    cursor: pointer;
    -webkit-user-select: none;
    user-select: none;
    background-color: rgba(57, 167, 230, 0.5);
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    border-radius: .25rem;
    
}
.img-over p{
    color: #1a1a1a;
}
.img-over{

    position: absolute;
    height: 0px; 
    width: 0px;
    background-color: rgb(255, 255, 255);
    padding: 50px;
    opacity: 0;
    color: #1a1a1a;
    overflow: hidden;
    /*
        -webkit-transition: all 400ms ease-in-out;
	-moz-transition: all 400ms cubic-bezier(0.1, 0.7, 1.0, 0.1);
	-o-transition: all 400ms cubic-bezier(0.1, 0.7, 1.0, 0.1);
	-ms-transition: all 400ms cubic-bezier(0.1, 0.7, 1.0, 0.1);
	transition: all 400ms cubic-bezier(0.1, 0.7, 1.0, 0.1);
    */ 
}
.img-big{
    height: 100%; 
    width: 100%;
    z-index: 999999999999999;
    position: fixed;
    /* -webkit-animation-name: zoomIn;
    animation-name: zoomIn; */
    opacity: 1;
    border-radius: 0%;
    transform: scale(0);
    border: solid 8px #26BDF8;
     animation: redondeos 0.5s ease-in-out normal forwards; 
}
@keyframes redondeos{
    0% {
        transform: scale(1);
    }
    25%{
        transform: scale(1.1);
    }
    50%{
        transform: scale(1.2);
    }
    75%{
        transform: scale(1.1);
    }
    100%{
        transform: scale(1);
    }
} 
.img-big-dos{
    height: 0%; 
    width: 0%;
    /* -webkit-animation-name: zoomOut;
    animation-name: zoomOut; */
    position: fixed;
    opacity: 0;
}
